<template>
  <div class="">
    <ul class="nav nav-tabs mb-4">
      <li
        v-for="tab in tabs"
        :key="tab.id"
        class="nav-item"
      >
        <router-link
          :to="tab.path"
          class="nav-link"
        >
        {{ tab.title }}
        </router-link>
      </li>
    </ul>
  </div>
</template>

<script>
  import {v4 as uuidv4} from 'uuid'
  export default {
    data () {
      return {
        tabs: [
          {
            id: uuidv4(),
            title: 'Pomofocus',
            path: '/'
          },
          {
            id: uuidv4(),
            title: 'Report',
            path: '/report'
          },
          {
            id: uuidv4(),
            title: 'Statistic',
            path: '/statistic'
          }
        ]
      }
    }
  }
</script>
