<template>
  <div class="container py-5">
    <NavTabs />
    <div class="row justify-content-center align-items-center" style="height: 600px;">
      <div class="home-page">
        <h1>Tomato Timer</h1>

        <div class="modes">
          <button class="btn btn-outline-primary" @click="pomofocus" :class="{active: this.mode === 'pomofocus'}">Pomofocus</button>
          <button class="btn btn-outline-warning ml-2" @click="shortBreak" :class="{active: this.mode === 'shortbreak'}">Short Break</button>
          <button class="btn btn-outline-info ml-2" @click="longBreak" :class="{active: this.mode === 'longbreak'}">Long Break</button>
        </div>

        <div class="inputTimer mt-3">
          <h3>Input Minites: 
            <input v-model="newTimer" @keyup.enter="inputTime" class="inputTime" id="inputTime" type="number" autofocus placeholder="minites...">
          </h3>
        </div>

        <div class="mt-5 mb-2" style="border-top: 1px solid black"></div>

        <div class="pickedTask">
          <h1 v-if="pickedTask">{{pickedTask.name}}</h1>
          <h1 v-else>Task</h1>
          <h3>{{showTime | toMinSecond}}</h3>
        </div>

        <div class="buttons">
          <button v-if="isStarted" type="button" class="btn btn-success" @click.stop.prevent="start">開始</button>
          <button v-else type="button" class="btn btn-danger" @click.stop.prevent="stopCountdown">暫停</button>
          <button type="button" class="btn btn-warning ml-2" @click.stop.prevent="reset">reset</button>
        </div>

        <div class="mt-5 mb-2" style="border-top: 1px solid black"></div>

        <Tasks 
          :tasks="tasks"
          @task-clicked="taskAfterClicked"
        />
      </div>
    </div>
  </div>
</template>

<script>
import Tasks from './../components/Tasks'
import NavTabs from './../components/NavTabs'

import axios from 'axios'

export default {
  components: {
    Tasks,
    NavTabs
  },
  data () {
    return {
      timer: null,
      showTime: -1,
      pomofocusTime: 2,
      shortBreakTime: 300,
      longBreakTime: 900,
      tempTime: 0,
      isStarted: true,
      mode: 'pomofocus',
      tasks: [],
      pickedTask: []
    }
  },
  created () {
    this.fetchTasks()
    this.showTime = this.pomofocusTime
  },
  methods: {
    async fetchTasks () {
      try {
        const { data } = await axios.get('/')
        this.tasks = data.tasks
      } catch (error) {
        console.log('error', error)
      }
    },
    start() {
      if (this.showTime === 0) {
        return
      }
      this.timer = setInterval(this.countDown, 1000)
      this.isStarted = false
    },
    async countDown() {
      this.showTime--
      if (this.mode === 'pomofocus' && this.showTime === 0) {
        this.pickedTask.period += this.pomofocusTime
        this.pickedTask.counter += 1
        await axios({
          method: 'put',
          url: `tasks/${this.pickedTask._id}`,
          data: { period: this.pickedTask.period, counter: this.pickedTask.counter }
        })
        .then(response => console.log(response))
        clearInterval(this.timer)
        this.shortBreak()
        this.reset()
      }
    },
    stopCountdown() {
      clearInterval(this.timer)
      this.isStarted = true
    },
    reset() {
      clearInterval(this.timer)
      if (this.mode === 'pomofocus') {
        this.showTime = this.pomofocusTime
      }
      else if (this.mode === 'shortbreak') {
        this.showTime = this.shortBreakTime
      }
      else if (this.mode === 'longbreak') {
        this.showTime = this.longBreakTime
      }
      this.isStarted = true
    },
    // 輸入的時間訂為"暫存時間"
    inputTime() {
      this.tempTime = this.newTimer.trim() * 60

      if (this.mode === 'pomofocus') {
        this.pomofocusTime = this.tempTime
        this.showTime = this.pomofocusTime
      } 
      else if (this.mode === 'shortbreak') {
        this.shortBreakTime = this.tempTime
        this.showTime = this.shortBreakTime
      } 
      else if (this.mode === 'longbreak') {
        this.longBreakTime = this.tempTime
        this.showTime = this.longBreakTime
      }

      this.newTimer = ''
    },

    // 模式切換
    pomofocus() {
      this.showTime = this.pomofocusTime
      this.mode = 'pomofocus'
    },
    shortBreak() {
      this.showTime = this.shortBreakTime
      this.mode = 'shortbreak'
    },
    longBreak() {
      this.showTime = this.longBreakTime,
      this.mode = 'longbreak'
    },
    taskAfterClicked(pickedTask) {
      console.log('pickedTask: ', pickedTask)
      this.pickedTask = pickedTask
      this.pomofocus()
    }
  },
  filters: {
    toMinSecond (time) {
      const minites = Math.floor(time / 60)
      const seconds = Math.floor(time % 60)
      if (minites < 10) {
        if (seconds < 10) {
          return `0${minites}:0${seconds}`
        }
        return `0${minites}:${seconds}`
      } else if (seconds < 10) {
        return `${minites}:0${seconds}`
      } else {
        return `${minites}:${seconds}`
      }
    }
  }
}
</script>